<template>
  <transition name="slide">
    <div class="user-center">
      <div class="back" @click="back">
        <i class="icon-back"></i>
      </div>
      <div class="switches-wrapper">
        <switches :switches="switches" :currentIndex="curIndex" @switch="switchChange"></switches>
      </div>
      <div ref="playBtn" class="play-btn" @click="random">
        <i class="icon-play"></i>
        <span class="text">随机播放全部</span>
      </div>
      <div class="list-wrapper" ref="listWrapper">
        <scroll v-if="curIndex === 0" :data="likeList" class="list-scroll" ref="likeList">
          <div class="list-inner">
              <song-list :songs="likeList" @select="selectSong"></song-list> 
          </div>
        </scroll>
        <scroll v-if="curIndex === 1" :data="playHistory" class="list-scroll" ref="playList">
          <div class="list-inner">
              <song-list :songs="playHistory" @select="selectSong"></song-list>
          </div>
        </scroll>
      </div>
      <div class="no-result-wrapper" v-show="noResult">
        <no-result :title="noResultText"></no-result>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
/* eslint-disable */

import Switches from "base/switches";
import Scroll from "base/scroll";
import SongList from "base/song-list";
import { mapGetters, mapActions } from "vuex";
import Song from "common/js/song";
import { playlistMixin } from "common/js/mixin";
import NoResult from "base/no-result";

export default {
  mixins: [playlistMixin],
  data() {
    return {
      switches: [{ name: "我喜欢的" }, { name: "最近听的" }],
      curIndex: 0
    };
  },
  computed: {
    noResult() {
      return this.curIndex === 0
        ? !this.likeList.length
        : !this.playHistory.length;
    },
    noResultText() {
      return this.curIndex === 0 ? "暂无收藏歌曲" : "你还没有听过歌曲";
    },
    ...mapGetters(["playHistory", "likeList"])
  },
  methods: {
    handlePlaylist(playlist) {
      const bottom = playlist.length > 0 ? "60px" : "";
      this.$refs.listWrapper.style.bottom = bottom;
      this.$refs.likeList && this.$refs.likeList.refresh();
      this.$refs.playList && this.$refs.playList.refresh();
    },
    back() {
      this.$router.back();
    },
    switchChange(index) {
      this.curIndex = index;
    },
    selectSong(song) {
      this.insertSong(new Song(song));
    },
    random() {
      let list = this.curIndex === 0 ? this.likeList : this.playHistory;
      if (list.length === 0) {
        return;
      }
      list = list.map(song => new Song(song));
      this.randomPlay({ list });
    },
    ...mapActions(["insertSong", "randomPlay"])
  },
  components: {
    Switches,
    Scroll,
    SongList,
    NoResult
  }
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/variable';

.user-center {
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 100;
  width: 100%;
  background: $color-background;

  &.slide-enter-active, &.slide-leave-active {
    transition: all 0.3s;
  }

  &.slide-enter, &.slide-leave-to {
    transform: translate3d(100%, 0, 0);
  }

  .back {
    position: absolute;
    top: 0;
    left: 6px;
    z-index: 50;

    .icon-back {
      display: block;
      padding: 10px;
      font-size: $font-size-large-x;
      color: $color-theme;
    }
  }

  .switches-wrapper {
    margin: 10px 0 30px 0;
  }

  .play-btn {
    box-sizing: border-box;
    width: 135px;
    padding: 7px 0;
    margin: 0 auto;
    text-align: center;
    border: 1px solid $color-text-l;
    color: $color-text-l;
    border-radius: 100px;
    font-size: 0;

    .icon-play {
      display: inline-block;
      vertical-align: middle;
      margin-right: 6px;
      font-size: $font-size-medium-x;
    }

    .text {
      display: inline-block;
      vertical-align: middle;
      font-size: $font-size-small;
    }
  }

  .list-wrapper {
    position: absolute;
    top: 110px;
    bottom: 0;
    width: 100%;

    .list-scroll {
      height: 100%;
      overflow: hidden;

      .list-inner {
        padding: 20px 30px;
      }
    }
  }

  .no-result-wrapper {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
  }
}
</style>


